button, .btn {
    border: none;
    background-color: $yellow;
    color: $black;
    padding: 10px 14px;
    cursor: pointer;
    font-size: 13px;
    transition: background-color 0.2s linear;
    &:hover {
        background-color: $lightyellow;
    }
}

.btn-prev, .btn-next {
    position: relative;
    margin-right: 6px;
    margin-top: -8px;
    z-index: 8;
}
.btn-show {
    margin-top: $dist-s;
}
.btn-close {
    background: none;
    border: 0;
    position: absolute;
    display: block;
    height: 40px;
    width: 40px;
    top: $dist-m;
    left: 10px;
    @media screen and (min-width: 768px) {
        left: $dist-m;
    }
    &:focus {
        outline: none;
    }
    &:hover {
        background-color: transparent;
        span:after {
            transform: rotate(180deg);
            
        }
        span:before {
            transform: rotate(-180deg);
        }    
    }
    span {
      display: block;
      position: absolute;
      text-indent: -999px;
      top: 50%;
      left: 50%;
      height: 40px;
      width: 40px;
      overflow: hidden;
      transform: translate(-50%, -50%);
      &:before, &:after {
            content: "";
            display: block;
            position: absolute;
            top: 14px;
            height: 2px;
            width: 40px;
            border-radius: 3px;
            border: 1px solid $yellow;
            background: $yellow;
            transform: rotate(135deg);
            transition: transform .2s linear;
        }
        &:before {
            transform: rotate(-135deg);
        }
    }
}